.do_button_dialog {
  display: none;
  background: #fff8f6;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.09);
  position: fixed;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
  display: flex;
  flex-direction: column;
  flex-axis: center;
  color: #4a4a4a;
  text-align: center;
}
.do_button_dialog .colse_dialog {
  position: absolute;
  top: 14px;
  right: 14px;
  cursor: pointer;
}
.do_button_dialog h1 {
  font-size: 30px;
  margin-bottom: 30px;
}
.do_button_dialog p {
/* 我們很遺憾您要取消訂單，為了提升網站與商: */
  font-size: 18px;
  letter-spacing: 0.47px;
}
.do_button_dialog .dialog_button_wrapper {
  display: flex;
  justify-content: center;
}
.do_button_dialog .dialog_button {
  margin: 0 20px;
}
.do_button_dialog .dialog_button {
  color: #fff;
  background: #c8837c;
}
@media all and (max-width: $phone_rwd_width) {
  .do_button_dialog {
    width: calc("100vw - 30px");
    box-sizing: border-box;
  }
  .do_button_dialog h1 {
    font-size: 20px;
  }
  .do_button_dialog p {
    font-size: 16px;
  }
}
